<template>
    <el-container v-loading="loading">
        <div class="rise x add question">
            <div class="header">
                <div class="title">{{title}}</div>
                <div class="item">
                    <span>问卷名称：</span>
                    <el-input v-model="name" placeholder="请输入问卷名称"/>
                </div>
                <div class="item" v-for="(item, index) in headerBtn" :key="index">
                    <button type="button" class="el-button btn el-button--primary" @click="headerClick(index)"><span>{{item}}</span></button>
                </div>
            </div>
            <div class="content">
                <div class="top">
                    <div class="item" v-for="(item, index) in data" :key="index">
                        <div class="item-name">
                            <span>{{item.name}}</span>
                            <span class="red" @click="themeDel(item, index)">删除</span>
                        </div>
                        <div class="item-opt">
                            <div v-for="(item2, index2) in item.options" :key="index2">{{item2.type}}:{{item2.content}}</div>
                        </div>
                    </div>
                </div>
                <div class="line"></div>
                <div class="article">
                    <div class="left">
                        <div class="item">
                            <span>题目类型：</span>
                            <el-select @change="selectChange" v-model="product.type" placeholder="请选择题目类型">
                                <el-option
                                    v-for="item in topicList"
                                    :key="item.id"
                                    :label="item.dValue"
                                    :value="item.id"
                                ></el-option>
                            </el-select>
                        </div>
                        <div class="item">
                            <span>题目名称：</span>
                            <el-input v-model="product.name" placeholder="请输入题目名称"/>
                            <div class="item-opt" v-for="(item, index) in product.options" :key="index">
                                <span>{{item.type}}:{{item.content}}</span>
                                <span class="red" @click="themeOptDel(item, index)">删除</span>
                            </div>
                        </div>
                        <div class="item" v-if="product.type == 0">
                            <span>选项：</span>
                            <el-select v-model="productOpt.type" placeholder="请选择选项">
                                <el-option
                                    v-for="item in topicOption"
                                    :key="item.id"
                                    :label="item.dValue"
                                    :value="item.dValue"
                                ></el-option>
                            </el-select>
                            <el-input v-model="productOpt.content" placeholder="请输入选项内容"/>
                            <button type="button" class="add el-button btn el-button--primary" @click="themeOptAdd()"><span>添加</span></button>
                        </div>
                        <div class="item">
                            <span></span>
                            <button type="button" class="add el-button btn el-button--primary" @click="themeClear()"><span>清空</span></button>
                            <button type="button" class="add el-button btn el-button--primary" @click="themeClick()"><span>出题</span></button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </el-container>
</template>

<script>
import queryString from "querystring";
import { DateDay } from 'utils/formData';
import { topicList, topicOption } from 'data/listBody';
const titles = ["新建", "编辑"];
const headerBtn = ["取消","保存"];
const tips = ['请补全选项内容','请补全问卷内容','请勿重复添加']

export default {
    name: "add",
    data() {
        return {
            title: "",
            id: "",
            headerBtn: headerBtn,
            topicList: topicList,   
            topicOption: topicOption,
            typeOption:'',
            loading: false,
            data: [], //问卷
            name: '', //问卷的名称
            product: { //当前题目
                type: '',
                name: '',               
                options: []
            },
            productOpt:{ //当前选项
                type:'',
                content: ''
            }
        };
    },
    mounted() {
        this.dataInit();
        // this.dropInit();
    },
    methods: {
        dataInit() {
            const { type, id } = this.$route.query;
            if (type !== "edit") return (this.title = titles[0]);
            this.title = titles[1];
            this.id = id;
            this.ajax();
        },
        headerClick(item){
            if(item == 0) this.$router.go(-1);
            if(item == 1) this.save();
        },        
        save() { //保存详情

        },
        themeOptAdd(){ //选项添加
            let opt = Object.assign({}, this.productOpt);
            let options = [].concat(this.product.options);
            let filter = options.filter(item => {
                return item.type === opt.type
            })
            if((!opt.type || !opt.content) && opt.type != 0) return this.$message(tips[0])
            if(filter.length > 0) return this.$message(tips[2])
            options.push(opt);
            this.product.options = options;
        },
        themeOptDel(obj, index){ //选项删除
            let options = [].concat(this.product.options);
            let filter = options.filter(item => {
                return obj.type !== item.type
            })
            this.product.options = filter;
        },
        themeClear(){ //试题清空
            this.product = {
                type: '',
                name: '',
                options: []
            }
            this.productOpt = {
                type: '',
                content: '',
            }
        },
        themeClick(){ //试题出题
            let opt = Object.assign({}, this.product);
            let data = [].concat(this.data);
            let filter = data.filter(item => {
                return item.name === opt.name
            })
            if((!opt.type || !opt.name) && opt.type != 0) return this.$message(tips[1])
            if(filter.length > 0) return this.$message(tips[2])
            data.push(opt);
            this.data = data;
        },
        themeDel(obj, index){ //试题删除
            let data = [].concat(this.data);
            let filter = data.filter(item => {
                return obj.name !== item.name
            })
            this.data = filter;
        },
        selectChange(e){ //题目类型切换
            this.themeClear()
            this.product.type = e;
        },
        ajax() {
            //查看详情
            // this.loading = true;
            // const param = queryString.stringify(params || {});
            // let url = urls[code];
            // this.api.get(`/role`).then((res)=>{
            // this.loading = false;
            // this.data = res.data.list;
            // this.setPage(res.data)
            // this.dataInit();
            // }).catch((err) => {
            //   console.log(err)
            //   this.$message(err.message)
            // })
        },
    }
};
</script>
 
<style lang="less" scoped>
.question.add{
    text-align: left;
    padding: 0 20px !important;
    position: absolute;
    height: calc(100% - 60px);
    width: calc(100% - 260px);
    overflow: hidden;
    top: 60px;
    .header{
        display: flex;
        justify-content: flex-start;
        .title{
            line-height: 70px;
            font-size: inherit;
            margin-bottom: 0;
        }
        .item{
            margin-right: 40px;
        }
    }
    .content{
        height: calc(100% - 70px);
        .top{
            height: 60%;
            overflow-y: auto;
            .item{
                flex-direction: column;
                align-items: flex-start;
                padding: 5px 0;
                .item-name{
                    font-weight: 600;
                    font-size: 18px;
                }
                .item-opt{
                    display: flex;
                    margin-top: 10px;
                    div{
                        margin-right: 20px;
                    }
                }
            }
        }
        .line{
            height: 2px;
            width: 100%;
            background: #eee;
            margin: 1% 0;
        }
        .article{
            height: 35%;
            overflow-y: auto;   
            .item{
                .el-input{
                    margin-right: 20px;
                }
                .item-opt{
                    margin-right: 20px;
                }
            }  
        }
    }
    .red{
        color: red;
        font-size: 12px;
        margin-left: 10px;
        cursor: pointer;
    }
    .el-select{
        margin-right: 20px;
    }
}
</style>
 